@import "../../../config/globals";
.todos-actions-bar{

    @include null-margins-paddings();
    display: flex;
    font-size: 1vw;
    color: $c-gray-dark;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    }

    .todos-actions-bar_counter-undone{
        padding-top: 12px;
        margin: 12px;
        height: 40px;

       }

    .todos-filters{
        height: 40px;
        margin: 12px;
        padding-top: 12px;

        }
        .todos-filter{
            font-size: 1vw;
            color: $c-gray-dark;
            padding: 0;
            padding-left: 3px;
            padding-right: 3px;
            margin: 0;
            box-sizing: border-box;
            border: 2px solid $c-white;
            border-radius: 2px;
            background: none;
            outline: none;

        }

            .todos-filter:hover,
            .todos-filter:focus{
                border: 2px solid $border-color;
                border-radius: 2px;
                }

        .todos-actions-bar_delete{
            height: 40px;
            margin: 12px;
            padding-top: 12px;

        }
            .todos-actions-bar_delete-done{
                display: inline;
                padding: 0px;
                margin: 0px;
                font-size: 1vw;
                color: $c-gray-dark;
                box-sizing: border-box;
                border: 2px solid $c-white;
                border-radius: 2px;

                background: none;
                outline: none;


            }

            .todos-actions-bar_delete-done:hover,
            .todos-actions-bar_delete-done:focus{
                border: 2px solid $border-color;
                border-radius: 2px;
                }
